@import url('https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@300;400;500;700&display=swap');

.primary-navigation.show{
    display: block;
}

.hamburger-btn.open .line:nth-child(1) {
    position: absolute;
    transform: rotate(135deg);
}

.hamburger-btn.open .line:nth-child(2){
    transform: scale(0);
}

.hamburger-btn.open .line:nth-child(3) {
    position: absolute;
    transform: rotate(-135deg);
}